html,
body {
  font-family: 'Open Sans', sans-serif !important;
  color: #000;
  height: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: normal;
}

.text {
  width: 96%;
  height: 200px;
  text-align: center;
  background: transparent;
  padding: 15px 5px;
  background-size: contain;
  margin-bottom: 20px;
}

.text p {
  font-size: 1.6rem;
  color: #222222;
  margin-bottom: 0;
  font-variant: small-caps;
  font-stretch: condensed;
}

.title {
  background: transparent;
  margin: 8px auto;
  text-transform: uppercase;
  font-weight: 800;
  font-size: 2rem;
  transform: scaleX(0.93);
  color: #2b2d2f;
  padding-bottom: 10px;
}

p {
  margin: 0;
}

a {
  color: inherit;
}

.lbody {
  height: 100%;
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
  background: url("bg.png") bottom no-repeat;
  background-size: cover;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}

.main {
  height: 240px;
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: url("back-main.png") center no-repeat;
  background-size: contain;
  margin-bottom: 30px;
  padding: 0 8px 9px 8px;
}

.footer {
  font-size: 16px;
  -ms-flex: 0 0 auto;
  -webkit-box-flex: 0;
  flex: 0 0 auto;
}

.container {
  display: flex;
  width: 100%;
  margin: 0 auto;
}

.header {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 0;
  left: 0;
  height: auto;
  width: 100%;
  overflow: hidden;
  box-shadow: none;
}

.headInfo {
  width: 100%;
  background: white;
  padding-bottom: 3px;
}

.headInfo__text {
  color: #7f7d7f;
  font-size: 1.1rem;
  font-weight: 400;
  transform: scaleX(0.93) scaleY(0.7);
  line-height: 12px;
  text-align: center;
}

.header__inner {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  max-height: 100%;
  min-height: 50px;
  height: 50px;
  background: whitesmoke;
  background-size: 100% 100%;
}

.info__text {
  font-family: Arial !important;
  font-size: 1.1rem;
  font-weight: 400;
  transform: scaleX(0.93) scaleY(0.7);
  line-height: 12px;
  text-align: center;
}

.header__icons {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 60%;
  height: 100%;
  font-weight: 400;
  margin-right: 10px;
}

.header__age {
  min-width: 40px;
  height: 100%;
  font-size: 1.7rem;
  font-weight: 400;
  display: flex;
  align-items: center;
  justify-content: center;
  background: url("18.png") center no-repeat;
  background-size: contain;
  color: #7f7d7f;
  border: none;
  border-radius: 0;
  z-index: 100;
}

.header__menu {
  width: 20px;
  height: 100%;
  margin-left: 15px;
  background: transparent;
  background-size: contain;
  color: #a4a4a4;
  font-size: 1.5rem;
  padding: 0;
  background-size: contain;
  border-radius: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header__notice {
  width: 50%;
  height: 100%;
  background: url("back-icons.png") center no-repeat;
  margin: 10px;
  background-size: contain;
  border-radius: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header__menu-holder {
  width: 20px;
  height: 12px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.header__menu-stick {
  height: 2px;
  width: 100%;
  background: #c5cbd1;
}

.header__item {
  padding: 15px 10px;
  color: #85969e;
  font-size: 1.5rem;
  transform: scaleY(0.9);
  width: 23%;
  height: 100%;
  background: #f3f3f3;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.divider {
  color: #e3e8ef;
  text-decoration: none;
  font-size: 1.3rem;
}

.underline {
  border-bottom: 1px solid;
  text-decoration: none;
}

.underline-color {
  border-bottom: 1px solid #b70025 !important;
}

.underline-bg {
  border-bottom: 2px solid;
}

.red {
  color: #b70025;
}

.header__img {
  display: block;
  max-width: 100%;
  height: auto;
}

.header__logo {
  width: 40%;
  height: 60%;
  font-size: 1.8rem;
  background: url("logo.png") left bottom no-repeat;
  background-size: contain;
  display: flex;
  align-items: center;
  margin-left: 10px;
}

.header__logo span {
  color: #f5e7ee;
  margin-right: 0px;
  transform: scaleX(0.9) scaleY(0.9);
}

.header__logo .red {
  color: #b70025;
  font-size: 1.8rem;
  font-weight: 600;
}

.spot {
  display: block;
  width: 5px;
  height: 5px;
  background: #b70025;
  border-radius: 20px;
  margin-right: 3px;
}

.header__logo h1 {
  font-weight: bold;
  text-align: center;
}

.video-top {
  width: 96%;
  height: 40px;
  top: 70px;
  left: 2%;
  background: transparent;
  background-size: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  z-index: -10;
}

.video-middle {
  width: 94%;
  height: 75%;
  top: 120px;
  bottom: none;
  left: 3%;
  background: transparent;
  background-size: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  z-index: -10;
}

.video-pre-bottom {
  width: 96%;
  height: 40%;
  top: 53%;
  left: 2%;
  right: 2%;
  background: transparent;
  background-size: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  z-index: -10;
}

.video-bottom {
  width: 96%;
  height: 35%;
  top: none;
  bottom: 11%;
  right: 2%;
  left: 2%;
  background: transparent;
  background-size: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  z-index: -10;
}

.video-header {
  margin: 15px auto;
  font-size: 2.3rem;
  color: white;
}

.top-menu-holder {
  width: 100%;
  background: #f3f3f3;
  position: absolute;
  height: 30px;
  top: 70px;
  left: 0;
}

.header__item-special {
  background: #4b5c73 !important;
  color: white !important;
  height: 110%;
}

.video-item {
  width: 150px;
  height: 100%;
  background-size: contain;
}

.block-top,
.block-bottom {
  height: 30px;
  width: 100%;
  margin: 0 auto;
  padding: 0 5%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.block-top {
  width: 96%;
  height: 30px;
  margin: 0 auto;
  padding: 0;
  background: transparent;
}

.lighter {
  color: #b7c5d7;
}

.loupe {
  margin-bottom: 35px;
}

.button__wrapper {
  background: transparent;
  margin-top: 0;
  background-size: contain;
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.wrapper__btn {
  width: 100%;
  height: auto;
  margin-top: 10px;
  margin-bottom: 5px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.button__submit {
  width: 95%;
  max-width: 340px;
  height: 30%;
  border: none;
  outline: none;
  font-size: 2.4rem;
  font-weight: 600;
  color: white;
  background: #e02b08;
  background-size: 100% 100%;
  text-transform: uppercase;
  text-decoration: none;
  border-radius: 40px;
  cursor: pointer;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 10px 20px;
  margin-bottom: 20px;
  margin-top: 40px;
  box-shadow: 11px 19px 68px 0px rgba(223, 47, 17, 0.42);
}

.button__submit:active {
  -webkit-box-shadow: -3px -3px 15px 0px rgba(0, 0, 0, 0.7);
  box-shadow: -3px -3px 15px 0px rgba(0, 0, 0, 0.7);
}

/*.block__info {
    display: inline-block;
    font-size: 2rem;
    color: $color-price;
    background-color: $back-price;
    border-radius: 2px;
    padding: 3px 8px;
}*/

.button__cancel {
  width: 100px;
  padding: 7px 3px;
  background-color: black;
  color: #f4f4f4;
  text-decoration: none;
  text-align: center;
  font-size: 20px;
  /* font-stretch: condensed;*/
  font-variant: all-small-caps;
  border: none;
  border-radius: 2px;
  transform: scaleX(0.93) scaleY(0.7);
}

.button__icon {
  width: 35px;
  height: 25px;
  background: url("button-icon.png") center no-repeat;
  background-size: contain;
}

.footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: black;
  line-height: 12px;
  text-align: center;
}

.footer__inner {
  text-align: center;
  color: #757575;
  font-size: 1.3rem;
  font-weight: 400;
  font-stretch: condensed;
  line-height: 100%;
  transform: scaleX(0.95) scaleY(0.75);
}

@media screen and (min-width: 765px) and (orientation: portrait) {
  .main {
    height: 260px;
    width: 98%;
  }

  .info__text {
    line-height: 14px;
    font-size: 1.4rem;
  }

  .video-top {
    height: 250px;
    width: 615px;
    top: 120px;
    left: 10%;
  }

  .button__submit {
    justify-content: center;
  }

  .button__icon {
    margin-left: 4%;
  }
}

@media screen and (min-width: 990px) and (orientation: landscape) {
  .main {
    height: 330px;
    width: 98%;
    background: url(back-main.png) center no-repeat;
    background-size: contain;
    margin-bottom: 260px;
  }
}

@media (orientation: landscape) and (max-width: 989px) {
  .lbody {
    background-position: top;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .loupe {
    margin-bottom: 0;
  }

  .top-menu-holder {
    display: none;
  }

  .main {
    justify-content: center;
    background: transparent;
    height: 100px;
    margin-bottom: 0;
    margin-top: 35px;
  }

  .button__wrapper {
    justify-content: flex-start;
    margin-top: 3%;
  }

  .video-middle,
  .video-pre-bottom,
  .video-top {
    display: none;
  }

  .video-bottom,
  .video-middle {
    display: block;
    top: 40px;
    height: 90%;
  }

  .footer__inner {
    line-height: 1;
  }

  .wrapper__btn {
    height: 60px;
    margin-top: 0;
    margin-bottom: 0;
  }

  .button__submit {
    justify-content: center;
    height: 100%;
    /*min-height: 70px*/
  }

  .button__icon {
    margin-left: 4%;
  }

  .header {
    height: 85px;
  }

  .header__logo {
    height: 70%;
    margin-top: 5px;
  }

  .button__cancel {
    margin-top: 30px;
  }
}

@media screen and (device-width: 320px) and (device-height: 480px) and (orientation: landscape) {
  /*  iPhone 4  */
}

@media screen and (min-height: 400px) and (max-height: 500px) and (orientation: portrait) {
  .main {
    height: 210px;
    margin-top: 50px;
  }

  .button__wrapper {
    margin-top: 50%;
  }

  .header__icons {
    width: 50%;
  }

  .header__notice {
    margin-right: 5px;
  }

  .video-top {
    top: 50px;
    height: 190px;
  }

  .video-middle {
    top: 90px;
  }

  .video-pre-bottom {
    top: 54%;
  }

  .video-bottom {
    height: 175px;
  }

  .top-menu-holder {
    top: 50px;
  }

  .text {
    height: 110px;
  }

  .title {
    font-size: 1.7rem;
  }

  .button__submit {
    font-size: 2rem;
  }

  .info__text {
    transform: scaleX(0.93) scaleY(0.9);
  }
}

@media screen and (min-height: 501px) and (max-height: 570px) and (orientation: portrait) {
  .button__wrapper {
